@import url('https://fonts.googleapis.com/css2?family=Big+Shoulders+Inline+Display:wght@700&family=Roboto:wght@400;500&display=swap');

html {
    box-sizing: border-box;
}
*,
*:before,
*:after {
    font-family: 'Big Shoulders Inline Display', sans-serif;
    box-sizing: inherit;
}
body {
    user-select: none;
    margin: 0;
    padding: 0;
    display: flex;
    flex-direction: column;
    min-height: 100vh;
    color: rgb(232, 232, 255);

    text-align: center;
}
h1 {
    margin: 1rem;
}
#header {
    font-size: 2.5rem;
}
#header,
#footer {
    height: 10%;
    background-color: rgb(27, 5, 122);
}

#main {
    background-color: rgba(1, 2, 20, 0.952);
    flex: auto;
    display: flex;
    background-image: url(./media/ship.png);
    background-size: max(80vmin, 60vmax);
    background-repeat: no-repeat;
    background-position: bottom;
    justify-content: center;
    width: 100%;
}
#audio {
    position: absolute;
    top: 4%;
    left: 90%;
}
.play-pause {
    width: 8vmin;
    cursor: pointer;
}
#welcome-screen {
    display: flex;
    flex-direction: column;
    gap: 1.5rem;
    margin-top: 6rem;
    font-size: 1.2rem;
}

input:focus {
    outline: none;
    color: ghostwhite;
    text-align: center;
}
#player-name {
    width: 30%;
    background-color: rgba(220, 220, 220, 0.356);
    border: none;
    border-radius: 12px;
    height: 3rem;
    font-size: 2rem;
}
#input-button {
    display: flex;
    align-items: center;
    justify-content: center;
}
.start-game {
    margin-left: 2rem;
    cursor: pointer;
    color: white;
    background-color: rgb(27, 5, 122);
    border: none;
    font-size: 3rem;
    border-radius: 10px;
}
.fleet {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    margin: 2vmin;
    width: 46vmin;

    font-size: 2rem;
    gap: 2vmin;
}
.rotate {
    cursor: pointer;
    width: 6vmin;
}
.drag {
    display: flex;
    z-index: 10;

    background-color: rgb(6, 20, 228);
}

.row-drag {
    flex-direction: row;
    border-radius: 1vmin 2.5vmin 2.5vmin 1vmin;
}
.col-drag {
    flex-direction: column;
    border-radius: 2.5vmin 2.5vmin 1vmin 1vmin;
}
.container-drag {
    height: calc(5 * 4.6vmin);
    display: flex;
    align-items: center;
}
.ship-square {
    width: 4.45vmin;
    height: 4.45vmin;
}
.player-container > h1,
.computer-container > h1 {
    margin: 0;
}
.player-container,
.computer-container {
    margin: 2vmin;

    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    gap: 2vmin;
    margin-bottom: 3rem;
}
.player-grid,
.computer-grid {
    display: grid;
    grid-template-columns: repeat(10, 4.6vmin);
    grid-template-rows: repeat(10, 4.6vmin);
    background-color: rgba(4, 191, 248, 0.918);
}
.reset {
    cursor: pointer;
    width: 100px;
}
.player-cell,
.computer-cell {
    border: 1px solid rgba(255, 255, 255, 0.521);
    position: relative;
}
a {
    color: white;
}
.shot {
    position: absolute;
    width: 2vmin;
    height: 2vmin;
    border-radius: 100%;
    left: 1.3vmin;
    top: 1.3vmin;
    display: flex;
    align-items: center;
    justify-content: center;
}
.yellow {
    background-color: yellow;
    z-index: 10;
}

.red {
    background-color: red;
    z-index: 10;
}

.end {
    align-self: center;
}
.restart {
    margin-top: 5rem;
    border: 3px solid black;
    border-radius: 10px;
    padding: 10px;
    background-color: rgb(27, 5, 122);
}
